












import Vue from 'vue';
import ItemsList from './components/ItemsList.vue';
import ItemDetail from './components/ItemDetail.vue';
import {GastronomyApi} from "@/api";
export default Vue.extend({
  components: {
    ItemsList,
    ItemDetail,
  },
  props: {
    contentIdList: {
      type: String,
      default: '1,2,3',
    },
  },
  data() {
    return {
      detailContentId: null,
    };
  },
  computed: {
    contentIds() {
      return this.contentIdList.split(',');
    },
    islistAvailable() {
      return this.contentIds.length > 1;
    },
  },
  created() {
    if (!this.islistAvailable) {
      this.detailContentId = this.contentIds[0];
    }
    this.loadGastronomyTypeList()
  },
  methods: {
    showDetail(detailContentId) {
      this.detailContentId = detailContentId;
    },
    closeDetail() {
      this.detailContentId = null;
    },
    loadGastronomyTypeList() {
      const gastronomyApi = new GastronomyApi()
      const gastronomyTypeList = gastronomyApi.gastronomyGetAllGastronomyTypesList()
      console.log(gastronomyTypeList)
    }
  },
});
